
.pricing {
  background-color: $gray;
  padding-top: 50px;
  padding-bottom: 45px;

  overflow: hidden;
}

.pricing__wrapper {
  width: 100%;
  overflow: hidden;

  @media (max-width: $mobile-width-only) {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.pricing__toggles {
  margin: 0;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.prices-table {
  background-color: $white;
  border: 2px solid $light-gray;
  border-radius: 0 0 0 1px;
  border-collapse: collapse;

  color: $midnight-blue;
  font-size: 14px;
  line-height: 30px;

  margin: auto;

  @media (max-width: $mobile-width-only) {
    table-layout: fixed;
    transform: translateX(-32%);
  }

  @media (min-width: $tablet-width) {
    margin: auto;
  }
}

.prices-table__row--header {
  background-color: $midnight-blue;
  color: $white;
  text-transform: uppercase;
}

.prices-table__item {
  border: 2px solid $light-gray;
  border-radius: 0 0 0 1px;

  min-width: 276px;
  padding-top: 24px;
  padding-left: 20px;
  padding-bottom: 25px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    padding-left: 15px;
    min-width: 152px;
  }

  @media (min-width: $desktop-width) {
    min-width: 233px;
  }
}

.prices-table__item--features {
  background-color: $gray;
  border-color: $gray;
  border-bottom: 2px solid $light-gray;
  font-size: 0;
}

.prices-table__amount {
  font-weight: 300;
}

@media (max-width: $mobile-width-only) {
  .prices-table__item--features,
  .prices-table__item:nth-child(1) {
    display: none;
  }
}
